import React from 'react';
import 'antd/dist/antd.css';
import { Descriptions, Radio, Button } from 'antd';
import {NavLink} from "react-router-dom";

class ProfileDescription extends React.Component {
    state = {
        size: 'default',
        editMode: false,
        profile: this.props.profile
    };

    onClick = () => {
        if(this.state.editMode===true) this.props.pushProfile(this.state.profile);
        this.setState({
            editMode: !this.state.editMode,
            profile: this.props.profile
        });
        //debugger

    }
    onChange = e => {
        this.setState({
            size: e.target.value,
        });
    };
    onChangeProfileProps = (p) => (e)=> {
        //debugger
        this.setState({
            profile: {...this.state.profile, [p] :e.target.value},
        });

    }
    onChangeProfileContactsProps = (p) => (e)=> {
        this.setState({
            profile: {...this.state.profile, contacts :{...this.state.profile.contacts,  [p] :e.target.value}},
        });
        e.target.value = this.state.profile.contacts[p];
    }
    InfoDataMap = Object.entries(this.state.profile.contacts).map(en => {

            return (
                <div>
                    {en[0] + " : "  + en[1]}
                    <br />
                </div>
            )
        })
    InputDataMap = Object.entries(this.state.profile.contacts).map(en => {

        return (
            <div>
                {en[0]}
                <input autoFocus={true}
                       value={this.state.profile.contacts[en[0]]}
                       onChange={this.onChangeProfileContactsProps(en[0])}
                />
            </div>
        )
    })

    render() {
        return (
            <div>
                <br />
                <br />

                    <Descriptions
                        fontcolor={"white"}
                        bordered
                        title="Информация о профиле"
                        size={this.state.size}
                        extra={<Button onClick={this.onClick} type="primary">Edit</Button>}
                    >
                        {this.state.editMode ?
                            <>
                                <Descriptions.Item label="Name">
                                    {<input autoFocus={true}
                                            value={this.state.profile.fullName}
                                            onChange={this.onChangeProfileProps('fullName')}/>}
                                </Descriptions.Item>
                                <Descriptions.Item
                                    label="Looking for a job">
                                    {<input autoFocus={true}
                                            value={this.state.profile.lookingForAJob}
                                            onChange={this.onChangeProfileProps('lookingForAJob')}/>}
                                </Descriptions.Item>
                                <Descriptions.Item label="Resume">
                                    {<input autoFocus={true}
                                            value={this.state.profile.lookingForAJobDescription}
                                            onChange={this.onChangeProfileProps('lookingForAJobDescription')}/>}
                                </Descriptions.Item>
                                <Descriptions.Item label="About me">
                                    {<input autoFocus={true}
                                            value={this.state.profile.aboutMe}
                                            onChange={this.onChangeProfileProps('aboutMe')}/>}
                                </Descriptions.Item>
                                <Descriptions.Item label="Contacts">
                                    {this.InputDataMap}
                                </Descriptions.Item>
                            </>

                        :
                        <>
                        <Descriptions.Item label="Name">{this.props.profile.fullName}</Descriptions.Item>
                        <Descriptions.Item
                            label="Looking for a job">{this.props.profile.lookingForAJob ? "Yes" : "No"}</Descriptions.Item>
                        <Descriptions.Item label="Resume">{this.props.profile.lookingForAJobDescription}</Descriptions.Item>
                        <Descriptions.Item label="About me">{this.props.profile.aboutMe}</Descriptions.Item>
                        <Descriptions.Item label="Contacts">
                            {this.InfoDataMap}
                        </Descriptions.Item>
                        </>}
                    </Descriptions>

                <br />
                <br />
            </div>
        );
    }
}

export default ProfileDescription;