import {uploadPhoto} from "../../../redux/profile-reducer";

export const Upload_button = (props) => {
    let onMyPhotoSelected = (e) => {
        if(e.target.files){props.uploadPhoto(e.target.files[0])};
    }
    return(
    <div>
        <input name="myFile" type="file" onChange={onMyPhotoSelected}/>
    </div>)
}